<script>
import TableComponent from "@/components/Table/Table.vue";
import PaginationComponent from "@/components/Pagination1/Pagination1.vue";

export default {
  name: "qualificationManagementRollCallComponent",
  components: { TableComponent, PaginationComponent },
  data() {
    return {
      schedule_no: "",
      available_begin_date: "",
      class_date: "",
      tableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "select_Date",
          label: "報名日期",
          width: "150",
        },
        {
          id: "info",
          label: "全稱(統一編號)",
          width: "150",
        },
        {
          id: "nameId",
          label: "身份證字號/性別/姓名",
          width: "200",
        },
        {
          id: "attend_Status",
          label: "上課狀態",
          width: "150",
        },
        {
          id: "file_Name",
          label: "入廠危害告知附檔",
          width: "150",
        },
        {
          id: "insuranceDate",
          label: "保險起迄時間",
          width: "150",
        },
        {
          id: "SStatus",
          label: "南廠申請型態",
          width: "150",
        },
        {
          id: "NStatus",
          label: "北廠申請型態",
          width: "150",
        },
      ],
      selectedList: [],
      detail: {},
      tableData: [],
      tableList: [],
    };
  },
  computed: {},
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    changePageHandler(page) {
      this.$store.state.currentPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
    },
    selectionChange(item) {
      this.selectedList = item;
    },
    async updAttendStatus(val) {
      this.selectedList.forEach((e) => (e.attend_Status = val));
      const res = await this.$store.dispatch(
        "updAttendStatus",
        this.selectedList
      );
      if (res.data.isOK) {
        this.$message({
          type: "success",
          duration: 5000,
          showClose: true,
          message: "修改成功",
        });
        this.qryQualifyDetail(
          this.schedule_no,
          this.available_begin_date,
          this.class_date
        );
        this.$store.state.currentPage = 1;
        this.$refs.multipleTable.$refs.multipleTable.clearSelection();
      }
    },
    async serviceCenterDownloadPdf(fileId) {
      try {
        const res = await this.$store.dispatch(
          "serviceCenterDownloadPdf",
          fileId
        );
        const href = URL.createObjectURL(res.data);
        const link = document.createElement("a");
        link.href = href;
        link.setAttribute("download", "入場危害告知附檔.pdf");
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        URL.revokeObjectURL(href);
      } catch (err) {
        console.log(err);
      }
    },
    async qryQualifyDetail(schedule_no, available_begin_date, class_date) {
      const res = await this.$store.dispatch("qryQualifyDetail", {
        schedule_no,
        available_begin_date,
        class_date,
      });
      const data = res.data.data;
      this.detail = data;
      if (data.course_Supplier || data.course_Supplier.length) {
        this.tableData = JSON.parse(JSON.stringify(data.course_Supplier));
        this.tableData.forEach((e, index) => {
          e.index = index + 1;
          e.info = `${e.s_Name || ""}/${e.s_id}`;
          e.nameId = `${e.id}/${e.sex || ""}/${e.p_Name || ""}`;
          e.date = `${e.insurance_Begin} ~ ${e.insurance_End}`;
          if (e.birthday) {
            e.birth =
              new Date(e.birthday).getTime() > new Date().getTime()
                ? ">18"
                : "<18";
          }
          e.insuranceDate = `${e.insurance_Begin} ~ ${e.insurance_End}`;
          e.SStatus = e.card36 === 0 ? "未製卡" : "已製卡";
          e.NStatus = e.card37 === 0 ? "未製卡" : "已製卡";
        });
        //this.tableList = this.tableData.slice(0, 10);
        //審核拒絕的就不再秀在點名畫面上了
        const filteredData = this.tableData.filter(
          (e) => e.approve_Status !== "2"
        );
        this.tableList = filteredData.slice(0, 10);
      }
    },
  },
  async mounted() {
    this.schedule_no = this.$route.params.schedule_no;
    this.available_begin_date = this.$route.params.available_begin_date;
    this.class_date = this.$route.params.class_date;
    this.qryQualifyDetail(
      this.schedule_no,
      this.available_begin_date,
      this.class_date
    );
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./rollCall.html"></template>
<style lang="sass" scoped src="./rollCall.sass"></style>
